.modal {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #0000004d;
  transition: all .5s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.modal__container {
  background-color: var(--color-beige);
  opacity: 0;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 50px;
  transition: all .5s ease-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.modal__label {
  flex-flow: column wrap;
  gap: 20px;
  display: flex;
}

