}

.modal__button {
  text-align: center;
  color: var(--color-beige);
  background-color: var(--color-swamp);
  border-radius: 6px;
  padding: 15px 40px;
  font-size: 16px;
  transition: all .3s ease-out;
  display: inline-block;
}

.modal__button:hover {
  color: var(--color-swamp);
  background-color: var(--color-orange);
}

.modal__close {
  width: 25px;
  height: 25px;
  background: url("cross.a8fd8492.svg") no-repeat;
